import React, { useState } from 'react';
import { HelmetProvider } from 'react-helmet-async';
import { Helmet } from 'react-helmet-async';
import '../../style/header.css';
import '../../style/footer.css';
import '../../style/Forms/Form.css';

import Header from '../../components/header.js';
import fundo from './fundo.png';
import axios from 'axios';
import Footer from '../../components/footer.js';

function App() {
  const [nome, setNome] = useState('');
  const [estado, setEstado] = useState('');
  const [festival, setFestival] = useState('');
  const [comentario, setComentario] = useState('');
  const [isSubmitting, setIsSubmitting] = useState(false); // Para evitar envios mltiplos

  // Funo para enviar os dados do formulrio ao backend
  const handleSubmit = async (e) => {
    e.preventDefault();

    // Dados do formulrio
    const formData = {
      nome,
      estado,
      festival,
      comentario,
    };

    try {
      setIsSubmitting(true); // Bloqueia mltiplos envios
      const response = await axios.post('http://localhost:5000/api/users', formData, {
        headers: {
          'Content-Type': 'application/json',
        },
      });

      console.log('Resposta do servidor:', response.data);
      alert('Formulrio enviado com sucesso!');

      // Limpar os campos do formulrio
      setNome('');
      setEstado('');
      setFestival('');
      setComentario('');
    } catch (error) {
      console.error('Erro ao enviar os dados:', error.response || error.message);
      alert('Erro ao enviar o formulrio. Por favor, tente novamente.');
    } finally {
      setIsSubmitting(false); // Libera o boto de envio
    }
  };

  return (
    <>
      <HelmetProvider>
        <div className="App">
          <Helmet>
            <title>Formulrio</title>
          </Helmet>
          <Header />

          <div className="section-form">
            <img src={fundo} alt="fundo" className="fundoAboutUS" />

            <h2> FORMULRIO PARA COMENTRIOS </h2>
            <div className="descricao">
              Envie por aqui seus comentrios sobre os festivais que voc frequenta ou que gostaria que estivessem no site, e sobre o projeto Festivale
            </div>

            <form onSubmit={handleSubmit} className="form">
              <div className="container">
                <div className="caixa">
                  <p className="n">Nome:</p>
                  <input
                    type="text"
                    value={nome}
                    onChange={(e) => setNome(e.target.value)}
                    required
                  />
                </div>

                <div className="caixa">
                  <p className="n">Estado:</p>
                  <input
                    type="text"
                    list="estados"
                    value={estado}
                    onChange={(e) => setEstado(e.target.value)}
                    required
                  />
                </div>

                <div className="caixa">
                  <p className="n">Festival:</p>
                  <input
                    type="text"
                    value={festival}
                    onChange={(e) => setFestival(e.target.value)}
                    required
                  />
                </div>
              </div>

              <div className="coment">
                <p>Sugesto/Comentrio:</p>
                <div className="area">
                  <textarea
                    value={comentario}
                    onChange={(e) => setComentario(e.target.value)}
                    placeholder="Comente aqui..."
                    required
                  />
                </div>
              </div>
              <button className="btn-enviar" type="submit" disabled={isSubmitting}>
                {isSubmitting ? 'Enviando...' : 'Enviar'}
              </button>
            </form>
          </div>

          <datalist id="estados">
            <option value="Acre" />
            <option value="Alagoas" />
            <option value="Amap" />
            <option value="Amazonas" />
            <option value="Bahia" />
            <option value="Cear" />
            <option value="Distrito Federal" />
            <option value="Esprito Santo" />
            <option value="Gois" />
            <option value="Maranho" />
            <option value="Mato Grosso" />
            <option value="Mato Grosso do Sul" />
            <option value="Minas Gerais" />
            <option value="Par" />
            <option value="Paraba" />
            <option value="Paran" />
            <option value="Pernambuco" />
            <option value="Piau" />
            <option value="Rio de Janeiro" />
            <option value="Rio Grande do Norte" />
            <option value="Rio Grande do Sul" />
            <option value="Rondnia" />
            <option value="Roraima" />
            <option value="Santa Catarina" />
            <option value="So Paulo" />
            <option value="Sergipe" />
            <option value="Tocantins" />
          </datalist>
          <Footer />
        </div>
      </HelmetProvider>
    </>
  );
}

export default App;



